<template>
  <div>
    <p>
      <strong>Enter A Postal Code to find the availability:</strong>
    </p>
    <form>
      <input
        class="form-control standard"
        type="text"
        v-model="search"
        placeholder="example: t8n5t9"
      />
      <input type="submit" class="btn btn-primary" @click="checkAvailability" />
      <br />
      <span class="d-inline-block text-danger p-3">{{ errorMSG }}</span>
    </form>

    <template v-if="loading">
      <div class="mx-auto">
        <p class="text-center">
          <span class="d-inline-block my-3"><strong>Loading</strong></span>
          <bounce-loader
            class="mx-auto my-3"
            :loading="loading"
            :color="loadingColor"
            :size="150"
          ></bounce-loader>
        </p>
      </div>
    </template>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "SearchBar",
  props: {
    availability: String
  },
  data() {
    return {
      search: "",
      errorMSG: "",
      loading: false,
      loadingColor: "#563d7c"
    };
  },
  methods: {
    checkAvailability(e) {
      e.preventDefault();
      this.errorMSG = "";
      if (this.search.length >= 5) {
        this.loading = true;
        axios
          .get(`${process.env.VUE_APP_HOST}availability/${this.search}`)
          .then(response => {
            this.loading = false;
            if (response.data.availability) {
              this.$emit("availabilityChange", response.data.availability);
            } else {
              this.errorMSG = `No dates available for ${this.search}. Please try another Postal Code.`;
            }
          })
          .catch(() => {
            this.loading = false;
            this.errorMSG = "Couldn't find availability.";
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.standard {
  max-width: 300px;
  display: inline-block;
}
</style>
