<template>
  <div class="home">
    <Header></Header>

    <h1 class="my-5">Check Availability</h1>
    <SearchBar @availabilityChange="availabilityChange" />
    <transition name="fade">
      <div v-if="isAvailable.length" class="gradient">
        <hr />

        <div class="p-4">
          <v-calendar is-expanded :attributes="attributes" />
        </div>
      </div>
    </transition>
    <Footer />
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import SearchBar from "@/components/SearchBar.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "home",
  components: {
    Header,
    SearchBar,
    Footer
  },
  data() {
    return {
      availability: [],
      attributes: []
    };
  },
  methods: {
    availabilityChange(value) {
      this.availability = value;
      let days = this.availability
        .splice(6)
        .map((value, index) => {
          return value ? index + 1 : value;
        })
        .filter(value => value != null);

      this.attributes = [
        {
          dot: "purple",
          popover: {
            label: this.availability[this.availability.length - 1],
            visibility: "hover",
            hideIndicator: true
          },
          dates: { weekdays: days }
        }
      ];
    }
  },
  computed: {
    isAvailable() {
      return this.availability.length > 0 ? this.availability : [];
    }
  }
};
</script>

<styles lang="scss">
    @import "../assets/scss/transitions.scss";

    .gradient {
      height: 100%;
      min-height:65vh;
      background-image: linear-gradient(#4272c1, #b56ffc, #fff, #fff, #fff);
    }
</styles>
